import gsap from 'gsap'

import BreadCrumb from '@/components/Blocks/BreadCrumb'
import FeaturedArticle from '@/components/Blocks/FeaturedArticle'
import ArticleCard from '@/components/Card/ArticleCard'
import ArticleNav from '@/components/Blocks/ArticleNav'
import axios from 'axios'

import functions from '@/compositions/functions'
const { loadImage } = functions()

export default {
    components: {
        BreadCrumb,
        FeaturedArticle,
        ArticleCard,
        ArticleNav,
    },
    async asyncData ({ $config, $axios, query, error }) {
        try {
            const res = await $axios.$get(`${$config.APP_BACKEND_API}/wp-json/api/projects/filter/?category=${query.category || ''}`)
            const data = res.data

            const seo = data.seo
            const totalPosts = data.posts
            const currentPosts = totalPosts.slice(0, 12)
            const totalPage = Math.ceil(totalPosts.length / 12)

            return {
                data,
                seo,
                totalPosts,
                currentPosts,
                totalPage,
            }
        } catch (e) {
            console.log(e)
            error({ statusCode: 404, message: 'Post not found' })
            return {
                data: {},
                seo: {},
                totalPosts: 0,
                currentPosts: '',
                totalPage: 0,
            }
        }
    },
    data () {
        return {
            currentPosts: [],
            totalPosts: [],
            currentPage: 1,
            totalPage: 0,
        }
    },
    head () {
        return {
            title: this.seo?.title,
            meta: [
                { hid: 'description', name: 'description', content: this.seo?.desc },
                { hid: 'og:title', property: 'og:title', content: this.seo?.title },
                { hid: 'og:description', property: 'og:description', content: this.seo?.desc },
                { hid: 'og:url', property: 'og:url', content: `${process.env.APP_URL}` },
                { hid: 'og:site_name', property: 'og:site_name', content: this.seo?.title },
                { hid: 'og:image', property: 'og:image', content: this.seo?.thumb ? this.seo?.thumb : `${process.env.APP_URL}/og_img.jpg` },
                { hid: 'og:image:alt', property: 'og:image:alt', content: this.seo?.desc },
                { hid: 'twitter:description', name: 'twitter:description', content: this.seo?.desc },
                { hid: 'twitter:title', name: 'twitter:title', content: this.seo?.title },
                { hid: 'twitter:image', name: 'twitter:image', content: this.seo?.thumb ? this.seo?.thumb : `${process.env.APP_URL}/og_img.jpg` },
            ],
        }
    },
    mounted () {
        this.$store.dispatch('ADD_LOADING_STACK', loadImage())
        this.$store.dispatch('WAIT_LOADING')

        this.gsapInit()
    },
    methods: {
        updatePage () {
            this.currentPage += 1
            const prevPostsLength = this.currentPosts.length
            const newPosts = this.totalPosts.slice((this.currentPage - 1) * 12, (this.currentPage - 1) * 12 + 12)
            this.currentPosts.push(...newPosts)

            this.$nextTick(function () {
                const els = gsap.utils.toArray('.projects__articles-item').slice(prevPostsLength)
                gsap.fromTo(els, { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 })
            })
        },
        updateCategory (category) {
            this.currentPage = 1

            this.$router.replace({ path: '/projects', query: { category } })

            axios.get(`${this.$config.APP_BACKEND_API}/wp-json/api/projects/filter/?category=${category || ''}`)
                .then((res) => {
                    this.totalPosts = res.data.data.posts
                    this.currentPosts = this.totalPosts.slice(0, 12)
                    this.totalPage = Math.ceil(this.totalPosts.length / 12)

                    this.$nextTick(function () {
                        const els = gsap.utils.toArray('.projects__articles-item')
                        gsap.fromTo(els, { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 })
                    })
                })
                .catch(() => {})
        },
        gsapInit () {
            gsap.fromTo('.breadcrumb', { y: -20, opacity: 0 }, { y: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.projects__title', { x: 20, opacity: 0 }, { x: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.projects__featured-article', { opacity: 0 }, { opacity: 1 }).delay(1)
            gsap.fromTo('.projects__featured-article .feature-article__blocks-block:first-child', { x: -20, opacity: 0 }, { x: 0, opacity: 1 }).delay(1.5)
            gsap.fromTo('.projects__featured-article .feature-article__blocks-block:last-child', { opacity: 0 }, { opacity: 1 }).delay(1.5)
            gsap.fromTo('.article-nav__menu-item', { x: 10, opacity: 0 }, { x: 0, opacity: 1, stagger: 0.2 }).delay(1.5)
            const tl = gsap.timeline()
            tl
                .fromTo('.projects__articles-item', { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 })
                .fromTo('.projects__button', { opacity: 0 }, { opacity: 1 }).delay(1.7)
        },
    },
}
